  margin: 0;
  padding: 0;
  box-sizing: border-box; }

:root {
  --bg:\$white;
  --text-color:\$black; }

.dark-theme {
  --bg:\$black;
  --text-color:\$grey; }

body {
  width: 100vw;
  min-height: 100vh;
  background-color: var(--bg);
  color: var(--text-color);
  font-family: "Poppins", sans-serif, sans-serif; }

header {
  background-color: #0d3b6b;
  padding-right: 3rem; }

nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 0rem;
  padding: 0 0.8rem; }
  nav ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 2rem; }
  nav a {
    text-decoration: none;
    color: inherit; }
    nav a:hover {
      color: #FFFFFF; }
    nav a:active {
      border-bottom: 2px solid #FFFFFF; }
  nav .icon {
    display: none; }

section .theme {
  width: 2rem;
  height: 0.5rem;
  border-radius: 0.8rem;
  transition: all 0.2s ease-in;
  background-color: white;
  padding: 5px;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  gap: 0rem;
  transition: all 0.2s ease-in; }

section .theme-switcher {
  width: 0.9rem;
  height: 0.9rem;
  background-color: #0d3b6b;
  border-radius: 50%; }

button {
  padding: 1rem;
  border: none;
  border-radius: 10px;
  background-color: #104882;
  color: #FFFFFF;
  cursor: pointer; }
  button:hover {
    color: #104882;
    background-color: #FFFFFF; }

.end {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 5rem;
  justify-content: start; }

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 2rem 0rem; }
  main div {
    max-width: 35rem;
    padding: 2rem;
    background-color: rgba(13, 59, 107, 0.05);
    border-radius: 1rem; }

@media (max-width: 768px) {
  .end button, .end ul {
    display: none; }
  nav .icon {
    display: block;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 0rem; }
    nav .icon svg:first-child {
      display: block; }
    nav .icon svg:last-child {
      display: none; } }

footer {
  background-color: #0d3b6b;
  padding: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0rem; }
